// 配置请求拦截器
import { AxiosError, AxiosRequestConfig, AxiosResponse } from "axios";
import * as Code from "@/http/code";
import cookieUtils from "@/utils/cookie";
import { message } from "@/components/elementUI/message";

export const requestInterceptor = (config: AxiosRequestConfig) => {
  qTouch.loading.start();
  let accessToken = cookieUtils.get("accessToken");
  if (accessToken) {
    config.headers.accessToken = "Bearer " + accessToken;
  } else {
    qTouch.navigateToUrl(`${qTouch.appPathMap.login}`);
  }
  return config;
};

export const requestErrorInterceptor = (error: AxiosError) => {
  // todo
  throw error;
};

export const responseInterceptor = (response: AxiosResponse) => {
  qTouch.loading.done();
  // todo
  // console.log("responseInterceptor");
  if (response.data.code === Code.TOKEN_EXPIRED) {
    qTouch.navigateToUrl(qTouch.appPathMap.login);
  }
  return response;
};

export const responseErrorInterceptor = (error: AxiosError) => {
  qTouch.loading.done();
  // todo
  // console.log("responseErrorInterceptor");
  let response = error.response;
  if (response) {
    if (response.status >= 500) {
      message.error("服务器错误");
    }
  }
  throw error;
};
