





















import { Upload, Dialog } from "element-ui";
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { ElUpload, ElUploadInternalFileDetail, HttpRequestOptions } from "element-ui/types/upload";
import Utils from "@/utils/utils";

@Component({
  components: { ElUpload: Upload, ElDialog: Dialog }
})
export default class QUpload extends Vue {
  @Prop({ type: String, default: "/api/file/upload/image" })
  active!: string;
  @Prop({ type: String, default: "" })
  accept!: string;
  @Prop({ type: Number, default: 9 })
  limitNum!: number;
  @Prop({ type: Number, default: 10 })
  size!: number;
  @Prop({ type: Boolean, default: true })
  prev!: boolean;
  @Prop({ type: Boolean, default: false })
  disabled!: boolean;

  @Model("input")
  fileList!: { name: string; url: string }[];

  fileLoading: boolean = false;
  dialogImageVisible: boolean = false;
  dialogImageUrl: string = "";

  fileLists: { name: string; url: string }[] = [];

  created() {
    // console.log(JSON.parse(this.fileList));
    // console.log(this.fileLists);
  }

  async validateImage(file: any) {
    const isJPEG = file.type === "image/jpeg";
    const isJPG = file.type === "image/jpg";
    const isPNG = file.type === "image/png";
    const isBMP = file.type === "image/bmp";
    const isLtSize = file.size / 1024 / 1024 < this.size;

    if (!(isJPEG || isJPG || isPNG || isBMP)) {
      return {
        status: false,
        message: "上传图片必须是jpg/jpeg/bmp/png格式!"
      };
    }
    if (!isLtSize) {
      return {
        status: false,
        message: "上传图片大小不能超过 " + this.size + " MB!"
      };
    }
    return {
      status: true,
      message: ""
    };
  }

  // 选择文件
  selectFile() {
    (this.$refs.fileInput as any).value = null; // 置空，防止删除后无法再次选择
    (this.$refs.fileInput as any).click();
    return true;
  }

  // 文件选取之后·
  async fileChange(el: any) {
    const file: any = [...el.target.files][0];
    let { status, message } = await this.validateImage(file);
    if (status) {
      this.customHttpRequest(file);
      this.fileLoading = true;
    } else {
      this.$message.error(message);
      return false;
    }
  }

  // 上传
  async customHttpRequest(file: any) {
    try {
      let fData = Utils.createUploadForm(file);
      let {
        data: { data, status, message }
      } = await this.$http.post(this.active, fData.formData, fData.config);
      if (status) {
        this.fileLists.push(data[0]);
      } else {
        this.$message.error(message);
      }
    } finally {
      this.fileLoading = false;
    }

    // this.$http
    //   .post(this.active, fData.formData, fData.config)
    //   .then((response: any) => {
    //     if (response.data.status) {
    //       console.log(response.data)
    //       // this.fileLists.push({
    //       //   name: response.data.data.fileName,
    //       //   url: response.data.data.accessUrl
    //       // });
    //     } else {
    //       this.$message.error("图片上传失败");
    //     }
    //     this.$emit("input", this.fileLists);
    //   })
    //   .finally(() => {
    //     this.fileLoading = false;
    //   });
  }

  imgPreview(file: any) {
    this.dialogImageUrl = file.url;
    this.dialogImageVisible = true;
  }

  imgRemove(index: number) {
    this.fileLists.splice(index, 1);
    this.$emit("input", this.fileLists);
  }

  @Watch("fileList", { deep: true, immediate: true })
  fileListChange() {
    this.fileLists = typeof this.fileList === "string" ? JSON.parse(this.fileList) : this.fileList;
    if (!this.fileLists) this.fileLists = [];
  }

  @Watch("fileLists", { deep: true, immediate: false })
  watchLength() {
    if (this.fileList && this.fileList.length > this.limitNum) {
      this.$message.warning(`最多可以上传【 ${this.limitNum} 】张图片！！`);
    }
    this.$emit("input", this.fileLists);
    this.$emit("change", this.fileLists);
  }
}
