















































































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import Utils from "@/utils/utils";
import cookieUtils from "@/utils/cookie";
import { Button, Col, Form, Select, Dialog, FormItem, Upload, Input, Option } from "element-ui";
import QUpload from "@/components/qTouch/upload/QUpload.vue";

@Component({
  components: {
    ElButton: Button,
    ElCol: Col,
    ElForm: Form,
    ElSelect: Select,
    ElDialog: Dialog,
    ElFormItem: FormItem,
    ElUpload: Upload,
    ElInput: Input,
    ElOption: Option,
    QUpload
  }
})
export default class QCustomInput extends Vue {
  // 子组件接收父组件传值
  @Prop({ type: Array, required: true })
  customOptions!: Array<any>;
  @Prop({ type: Boolean, default: false })
  disabled!: boolean;
  @Prop({ type: Boolean, default: false })
  closable!: boolean;
  @Prop({ type: Number, default: 3 })
  lineSize!: number;
  @Prop({ type: String, default: "medium" })
  size!: string;

  customsForm: any = {
    customs: [],
    name: "userDefined"
  };

  // 验证规则
  rule: any[] = [
    {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    }
  ];
  loading: boolean = false;
  // 定位相关
  // 地图样式相关
  currentValueIndex = -1;
  map: any;
  center: number[] = [106.569137, 29.643814];
  zoom: number = 16;
  mapStyle: string = "amap://styles/1ee930c108ebbd53691071ed7b36cdac";
  marker: any = null;
  addressDialog = false;
  text = "建筑物坐标绑定";

  @Watch("customOptions", { deep: true, immediate: true })
  dataTypeConversion() {
    if (this.customOptions.length > 0) {
      Utils.convertFromExtInfoVO(this.customOptions);
      this.customsForm.customs = this.customOptions;
    } else {
      this.customsForm.customs = [];
    }
  }

  get styles() {
    if (this.lineSize === 1) {
      return { width: "100%" };
    }
    if (this.lineSize === 2) {
      return { width: "50%" };
    }
    return { width: "33.3%" };
  }

  // 定位相关
  // 打开弹窗，选择定位
  locationSelect(i: number) {
    this.currentValueIndex = i;
    this.addressDialog = true;
    this.$nextTick(() => {
      this.text = "请在地图上选取建筑物位置，并双击确定！";
      // 调用地图交互事件
      this.initMap();
      setTimeout(() => {
        this.chooseSiteOnMap();
      }, 100);
    });
  }

  // 开始地图交互事件
  chooseSiteOnMap() {
    let _this = this;
    this.text = "请在地图上选取建筑物位置，并双击确定！";
    // this.map.clearMap();
    // 修改鼠标样式( 十字型 )
    this.map.setDefaultCursor("crosshair");
    this.map.on("dblclick", function(e: any) {
      let lnglat = [e.lnglat.getLng(), e.lnglat.getLat()];
      _this.text = "您选取了 " + JSON.stringify(lnglat) + " 的位置！";
      let geocoder = new (window as any).AMap.Geocoder({
        radius: 1000,
        extensions: "all"
      });
      geocoder.getAddress(lnglat, function(status: any, result: any) {
        if (status === "complete" && result.info === "OK") {
          console.log(result);
          let address = result.regeocode.formattedAddress; //返回地址描述
          _this
            .$prompt("当前选中位置：", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              inputValue: address
            })
            .then((obj: any) => {
              _this.customsForm.customs[_this.currentValueIndex].value = {
                address: obj.value,
                lng: e.lnglat.getLng(),
                lat: e.lnglat.getLat()
              };
              _this.addressDialog = false;
            })
            .catch(() => {
              _this.text = "请在地图上选取位置，并双击确定！";
            });
          // _this.addressDialog = false;
        }
      });
    });
  }

  // 载入地图
  initMap() {
    const _this = this;
    this.map = new (window as any).AMap.Map(this.$refs["locationMap"], {
      center: this.center,
      resizeEnable: true,
      zoom: this.zoom
    });
    (window as any).AMapUI.loadUI(["misc/PoiPicker"], function(PoiPicker: any) {
      var poiPicker = new PoiPicker({
        input: "posInput"
      });
      //初始化poiPicker
      _this.poiPickerReady(poiPicker);
    });
  }

  // 根据定位选取地址信息
  poiPickerReady(poiPicker: any) {
    const _this = this;
    (window as any).poiPicker = poiPicker;
    let marker = new (window as any).AMap.Marker();
    // 选取了某个POI
    poiPicker.on("poiPicked", function(poiResult: any) {
      let source = poiResult.source;
      let poi = poiResult.item;
      let info = {
        source: source,
        id: poi.id,
        name: poi.name,
        location: poi.location.toString(),
        address: poi.formattedAddress
      };
      marker.setMap(_this.map);
      marker.setPosition(poi.location);
      _this.map.setCenter(marker.getPosition());
    });
  }

  // 数字框最大值最小值限制
  async checkValueLegal(data: any) {
    // 如果该项是必填项或输入有值时，大于最大值时取最大值，小于最小值时取最小值,如果不是必填项，就什么都不管
    if (data.mustInput || data.value) {
      if (data.value < data.dataRange.numMin) {
        data.value = data.dataRange.numMin;
        this.$message.warning("输入不能小于最小值");
      }
      if (data.value > data.dataRange.numMax) {
        data.value = data.dataRange.numMax;
        this.$message.warning("输入不能大于最大值");
      }
    }
  }

  // 表单验证方法
  formValidation() {
    return new Promise((resolve, reject) => {
      let formRef: any = this.$refs["customsForm"];
      formRef.validate((valid: boolean) => {
        if (valid) {
          if (!this.customOptions) return {};
          let transformData = this.customOptions.map((item: any) => {
            switch (item.attrType) {
              case "multiSelect":
                return {
                  ...item,
                  value: item.value.toString()
                };
              case "select":
                return {
                  ...item,
                  value: typeof item.value === "number" ? item.value.toString() : item.value
                };
              case "image":
                //处理上传图片
                return {
                  ...item,
                  value: JSON.stringify(item.value)
                };
              case "location":
                return {
                  ...item,
                  value: JSON.stringify(item.value)
                };
              case "number":
                if (item.value < item.dataRange.numMin) {
                  item.value = item.dataRange.numMin;
                  this.$message.warning("输入不能小于最小值");
                  reject();
                }
                if (item.value > item.dataRange.numMax) {
                  item.value = item.dataRange.numMax;
                  this.$message.warning("输入不能大于最大值");
                  reject();
                }
                return {
                  ...item,
                  value: typeof item.value === "number" ? item.value.toString() : item.value
                };
              default:
                // eslint-disable-next-line no-case-declarations
                return {
                  ...item
                };
            }
          });
          let tempObject = JSON.parse(JSON.stringify(this.customOptions));
          Utils.convertToExtInfo(tempObject);
          resolve(tempObject);
        } else {
          reject("有必填项未填写");
        }
      });
    });
  }

  removeItem(item: any) {
    let index = this.customsForm.customs.indexOf(item); // 删除选项
    this.$confirm("将要删除项目:" + item.name + ",是否继续？", "确认", {
      confirmButtonText: "确定",
      cancelButtonText: "取消"
    }).then(() => {
      if (index !== -1) {
        this.customsForm.customs.splice(index, 1);
      }
      this.$emit("input", this.customsForm.customs);
      this.$emit("change", this.customsForm.customs);
    });
  }

  destroyMap() {
    this.map.destroy();
    console.log(this.map);
  }
}
