export const formatterAttrType = (data: any) => {
  switch (data) {
    case "select":
      return "单项选择";
    case "multiSelect":
      return "多项选择";
    case "number":
      return "数值";
    case "image":
      return "图片";
    case "text":
      return "文本";
    case "location":
      return "定位";
  }
};
