












import { Card } from "element-ui";
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

@Component({
  name: "QCard",
  components: { Card }
})
export default class QCard extends Vue {
  @Prop({ type: String })
  title!: String;
  @Prop({ type: String, default: "info" })
  icon!: String;
  @Prop({ type: String })
  btnTip!: string;
}
