 *
 * @author
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { Button, Dialog } from "element-ui";

@Component({
  components: {
    ElButton: Button,
    ElDialog: Dialog
  }
})
export default class QPosition extends Vue {
  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;
  @Prop({ type: String })
  location!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addressDialog = newValue;
    if (newValue && !this.isMounted) {
      this.locationSelect();
    }
  }

  @Watch("location")
  handleChangeStatus(val: any) {
    if (val && val.length && this.map) {
      this.map.setCenter(JSON.parse(val).position);
      let marker = new (window as any).AMap.Marker({
        position: JSON.parse(val).position
      });
      this.map.add(marker);
      marker.setLabel({
        offset: new (window as any).AMap.Pixel(20, -20), //设置文本标注偏移量
        content: `<div class='info'>${JSON.parse(val).address}</div>`, //设置文本标注内容
        direction: "right" //设置文本标注方位
      });
    }
  }

  // 定位相关
  // 地图样式相关
  currentValueIndex = -1;
  map: any;
  center: number[] = [106.569137, 29.643814];
  zoom: number = 16;
  mapStyle: string = "amap://styles/normal";
  marker: any = null;
  addressDialog = false;
  text = "建筑物坐标绑定";
  isMounted: boolean = false;

  closeDialog() {
    // this.map.destroy();
    this.$emit("close");
  }

  // 打开弹窗，选择定位
  locationSelect() {
    this.$nextTick(() => {
      this.text = "请在地图上选取建筑物位置，并双击确定！";
      // 调用地图交互事件
      this.initMap();
      this.isMounted = true;
      setTimeout(() => {
        this.chooseSiteOnMap();
      }, 100);
    });
  }

  // 开始地图交互事件
  chooseSiteOnMap() {
    let _this = this;
    this.text = "请在地图上选取建筑物位置，并双击确定！";
    // this.map.clearMap();
    // 修改鼠标样式( 十字型 )
    this.map.setDefaultCursor("crosshair");
    this.map.on("dblclick", function(e: any) {
      let lnglat = [e.lnglat.getLng(), e.lnglat.getLat()];
      _this.text = "您选取了 " + JSON.stringify(lnglat) + " 的位置！";
      let geocoder = new (window as any).AMap.Geocoder({
        radius: 1000,
        extensions: "all"
      });
      geocoder.getAddress(lnglat, function(status: any, result: any) {
        if (status === "complete" && result.info === "OK") {
          let address = result.regeocode.formattedAddress; //返回地址描述
          _this
            .$prompt("当前选中位置：", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              inputValue: address
            })
            .then((obj: any) => {
              _this.dialogVisible && _this.$emit("update", obj.value, result, lnglat);
              _this.dialogVisible && _this.$emit("input", obj.value, result, lnglat);
              _this.dialogVisible && _this.$emit("change", obj.value, result, lnglat);
              _this.addressDialog = false;
            })
            .catch(() => {
              _this.text = "请在地图上选取位置，并双击确定！";
            });
        }
      });
    });
  }

  // 载入地图
  initMap() {
    const _this = this;
    this.map = new (window as any).AMap.Map("formlocationMap", {
      center: this.location ? JSON.parse(this.location).position : this.center,
      zoom: this.zoom,
      features: ["bg", "point", "road", "building"]
    });
    (window as any).AMapUI.loadUI(["misc/PoiPicker"], function(PoiPicker: any) {
      var poiPicker = new PoiPicker({
        input: "posInput"
      });
      //初始化poiPicker
      _this.poiPickerReady(poiPicker);
    });
  }

  // 根据定位选取地址信息
  poiPickerReady(poiPicker: any) {
    const _this = this;
    (window as any).poiPicker = poiPicker;
    let marker = new (window as any).AMap.Marker();
    // 选取了某个POI
    poiPicker.on("poiPicked", function(poiResult: any) {
      let source = poiResult.source;
      let poi = poiResult.item;
      let info = {
        source: source,
        id: poi.id,
        name: poi.name,
        location: poi.location.toString(),
        address: poi.formattedAddress
      };
      marker.setMap(_this.map);
      marker.setPosition(poi.location);
      _this.map.setCenter(marker.getPosition());
    });
  }

  beforeDestroy() {
    if (this.map) this.map.destroy();
  }
}
