




































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { Tag } from "element-ui";
import { ElTree } from "element-ui/types/tree";

@Component({
  components: { ElTag: Tag }
})
export default class QTree extends Vue {
  // 树对应 data
  @Prop({ type: Array, required: true })
  treeData!: any[];
  // 树对应 Prop
  @Prop({ type: Object, required: true })
  treeProp!: any;
  // 树对应 node-key
  @Prop({ type: String, default: "id", required: false })
  nodeKey!: string;
  // 弹出框 title
  @Prop({ type: String, default: "组织类型", required: false })
  title!: string;
  // 树是否可多选
  @Prop({ type: Boolean, default: false, required: false })
  multi!: boolean;
  // 已选中标签是否可以删除
  @Prop({ type: Boolean, default: true, required: false })
  closable!: boolean;
  // 是否需要转为字符串
  @Prop({ type: Boolean, required: true })
  needStringify!: boolean;
  @Prop({ type: Boolean, default: false })
  disabled!: boolean;

  @Model("input")
  value!: any;

  checkedValues: any[] = []; // 保存已选中的节点数组
  checkedNames: string[] = [];
  dialogVisible: boolean = false;

  initializeData: any; // 保存初始化数据

  @Watch("value", { immediate: true, deep: true })
  reSetInit() {
    if (this.value) {
      if (this.needStringify) {
        this.initializeData = this.value.split(",");
      } else {
        this.initializeData = this.value;
      }
    } else {
      this.initializeData = [];
    }
    this.checkedNames = []; // 置空，避免重复push
    this.initializeData.forEach((n: string) => this.initCheckedNames(n, this.treeData));
  }
  @Watch("treeData", { immediate: true, deep: true })
  reSetTreeData(newArr: any[]) {
    if (this.value) {
      if (this.needStringify) {
        this.initializeData = this.value.split(",");
      } else {
        this.initializeData = this.value;
      }
    } else {
      this.initializeData = [];
    }
    this.checkedNames = []; // 置空，避免重复push
    this.initializeData.forEach((n: string) => this.initCheckedNames(n, newArr));
  }

  // 初始化标签
  initCheckedNames(id: string, data: any[]) {
    data.forEach((i: any) => {
      if (id === i[this.treeProp.value]) {
        this.checkedNames.push(i[this.treeProp.label]);
      }
      if (i.children) {
        this.initCheckedNames(id, i.children);
      }
    });
  }

  // 打开弹窗时初始化选中数据
  initTreeChecked() {
    this.dialogVisible = true;
    this.$nextTick(() => {
      (this.$refs["QTree"] as any).setCheckedKeys(this.initializeData);
    });
  }

  // 点击多选框时调用
  handleNodeChange(node: any, checked: boolean) {
    if (checked && !this.multi) {
      (this.$refs["QTree"] as any).setCheckedKeys([node[this.treeProp.value]]);
    }
    this.checkedValues = (this.$refs["QTree"] as any).getCheckedNodes();
  }

  // 点击确定后修改初始化数据
  hasCheckedNodes() {
    this.checkedNames = [];
    this.initializeData = this.checkedValues.map((i: any) => i[this.treeProp.value]);
    this.checkedNames = this.checkedValues.map((i: any) => i[this.treeProp.label]);
    this.doEmit();
    // 返回确定按钮点击事件
    this.$emit("qClick");
    this.dialogVisible = false;
  }

  // 移除单个标签
  removeItem(index: number) {
    this.initializeData.splice(index, 1);
    this.checkedNames.splice(index, 1);
    this.doEmit();
    // 返回确定按钮点击事件
    this.$emit("qRemove");
  }

  // 返回已选中数据
  doEmit() {
    if (this.needStringify) this.$emit("input", this.initializeData.join(","));
    else {
      this.$emit("input", this.initializeData);
      this.$emit("change", this.initializeData);
      this.$emit("blue", this.initializeData);
    }
  }
}
