







import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { Button } from "element-ui";

@Component({
  components: {
    ElButton: Button
  }
})
export default class SearchButton extends Vue {
  @Prop({ type: String, default: "el-icon-info" })
  icon?: string;
  @Prop({ type: String })
  barTitle!: string;
  @Prop({ type: String })
  btnName!: string;
}
