// import store from "@/store";
import cookieUtils from "@/utils/cookie";

export default {
  //处理子节点为空的数据，将子节点删除
  removeEmptyChildren(data: Array<any>) {
    data.forEach((item: any) => {
      if (item.children) {
        if (item.children.length == 0) {
          delete item.children;
        } else {
          this.removeEmptyChildren(item.children);
        }
      } else {
        delete item.children;
      }
    });
  },
  //返回数组的最后一个元素
  lastOfArr(arr: Array<any>) {
    if (arr.length > 0) {
      return arr[arr.length - 1];
    }
    return null;
  },
  //将数据库返回的自定义信息转换成el格式
  convertFromExtInfoVO(extInfoVO: any) {
    if (extInfoVO) {
      extInfoVO.forEach((item: any) => {
        if (typeof item.dataRange === "string") {
          item.dataRange = JSON.parse(item.dataRange);
        }
        // item.value为null,表示新增的时候，默认值勾选上，item.value为空字符串，表示删除所有值，为其他表示自己勾选的值
        switch (item.attrType) {
          case "multiSelect":
            if (!item.value && item.value !== "") {
              item.value = item.dataRange.options.filter((i: any) => i.defaultChosen).map((n: any) => n.content);
            }
            if (typeof item.value === "string" && item.value !== "") {
              item.value = item.value.split(",");
            } else if (item.value === "") {
              item.value = [];
            }
            break;
          case "select":
            if (!item.value) {
              let value = item.dataRange.options.filter((i: any) => i.defaultChosen)[0];
              item.value = value ? value.content : "";
            }
            break;
          case "image":
            if (typeof item.value === "string") item.value = item.value ? JSON.parse(item.value) : null;
            else item.value = item.value ? item.value : null;
            break;
          case "text":
            item.value = item.value === null ? (item.dataRange.presetText ? item.dataRange.presetText : "") : item.value;
            break;
          case "location":
            if (!item.value) {
              item.value = "";
            }
            break;
          default:
            return item.value;
        }
      });
    }
  },
  //将el格式自定义数据转换成数据库格式
  convertToExtInfo(extInfoVO: any) {
    extInfoVO.forEach((item: any) => {
      switch (item.attrType) {
        case "multiSelect":
          item.value = item.value.toString();
          break;
        case "select":
          item.value = typeof item.value === "number" ? item.value.toString() : item.value;
          break;
        case "image":
          //处理上传图片
          item.value = JSON.stringify(item.value);
          break;
        case "location":
          item.value = JSON.stringify(item.value);
          break;
        default:
          // eslint-disable-next-line no-case-declarations
          return item.value;
      }
    });
  },
  // 删除数组指定元素
  removeArrItemByValue(arr: any, value: any) {
    let idx = arr.indexOf(value);
    arr.splice(idx, 1);
  },
  //组装图片上传form
  createUploadForm(file: any) {
    let res = {
      config: {
        headers: {
          accessToken: "Bearer " + cookieUtils.get("accessToken"),
          "Content-Type": "multipart/form-data"
        }
      },
      formData: {}
    }; //添加请求头
    let formData = new FormData();
    formData.append("file", file);
    res.formData = formData;
    return res;
  },
  //合并对象
  mapValue(obj: any, data: any) {
    for (let i in data) {
      if (i in obj) {
        obj[i] = data[i];
      }
    }
  },

  //对象深拷贝
  deepCopyObjtoObj(obj1: any, obj2: any) {
    for (let o of Object.getOwnPropertyNames(obj1)) {
      if (typeof obj1[o] == "object" && typeof obj2[o] == "object") {
        let m: Object = obj1[o];
        let n: Object = obj2[o];
        this.deepCopyObjtoObj(m, n);
        obj2[o] = m;
      } else {
        continue;
      }
    }
    Object.assign(obj1, obj2);
  },

  //将当前菜单数据扁平化，以hash值存放在对象里
  convertMenus(menu: Array<any>, menuMap: any) {
    menu.forEach((item: any) => {
      if (item.children && item.children.length > 0) {
        this.convertMenus(item.children, menuMap);
      } else {
        menuMap[qTouch.basePath + item.path] = true;
      }
    });
  },

  // 根据id查找对应的name
  getNameById(id: string, arr: Array<any>) {
    let name: string = "";
    arr.forEach((li: any) => {
      if (li.id === id) {
        name = li.name;
        return;
      } else if (li.children && li.children.length) {
        name = this.getNameById(id, li.children) ? this.getNameById(id, li.children) : name;
      }
    });
    return name;
  }
};
