import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import { formatterAttrType } from "@/utils/dataFormat";

@Component({
  filters: { formatterAttrType }
})
export default class BaseDataInfoTable extends Vue {
  @Prop({ type: String })
  active!: string;
  @Prop({ type: String, default: "/admin/typeDetail/saveCustomAttrItems" })
  url!: string;
  @Prop({ type: String })
  id!: string;

  doLoading: boolean = true;
  // 分页与筛选
  pagination: any = {
    condition: "",
    currentPage: 1,
    total: 0,
    tableData: [],
    pageSize: 10
  };

  @Watch("id", { immediate: true })
  async getCustomAttributeList() {
    if (!this.id) return;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(`${this.active}${this.id}`, {
        current: this.pagination.currentPage,
        size: 10,
        query: {
          name: this.pagination.condition,
          code: "",
          attrType: ""
        }
      });
      if (status) {
        this.pagination.tableData = data.records;
        this.pagination.total = data.total;
      } else {
        this.$message.error(message);
      }
      this.doLoading = false;
    } catch (e) {
      this.$message.error("请求失败，请重试！");
    } finally {
      this.doLoading = false;
    }
  }

  handleCurrentChange(current: number) {
    this.pagination.currentPage = current;
    this.getCustomAttributeList();
  }

  changeRequiredStatus(item: any) {
    this.$http
      .post(this.url, item)
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("操作成功");
        } else {
          item.mustInput = !item.mustInput;
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        item.mustInput = !item.mustInput;
      });
  }
}
